.main-wrapper {
  padding-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 700px) {
  .main-wrapper {
    padding-top: 4rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: auto;
    grid-gap: 1rem;
  }
}
